import Vue from "vue";
import VueRouter from "vue-router";
import {
  Home,
  CheckListExplorer,
  CheckListPage,
  QuestionnaireExplorer,
  QuestionnairePage
} from "@/components/pages";
import paths from "@/router/paths";

Vue.use(VueRouter);

const routes = [
  {
    path: paths.homeDefault,
    component: Home,
    name: "home"
  },
  {
    path: paths.checkListExplorer,
    component: CheckListExplorer
  },
  {
    path: paths.rawCheckListPage,
    component: CheckListPage,
    name: "checkList"
  },
  {
    path: paths.questionnaireExplorer,
    component: QuestionnaireExplorer
  },
  {
    path: paths.rawQuestionnairePage,
    component: QuestionnairePage,
    name: "questionnaire"
  }
];

const router = new VueRouter({
  routes
});

export default router;
