import Vue from "vue";
import { Component, Inject } from "vue-property-decorator";
import { AddButton } from "@/components/elements";
import { QuestionnaireDatabaseService } from "@/store/service";
import { ResponsibilityHelper } from "@/store";
import { QuestionnaireCard } from "@/components/cards";

interface Questionnaire {
  id: string;
  data: {};
}

@Component({
  components: {
    AddButton,
    QuestionnaireCard
  }
})
export default class QuestionnaireExplorer extends Vue {
  @Inject(QuestionnaireDatabaseService.name)
  questionnaireDatabaseService!: QuestionnaireDatabaseService;

  questionnaireCards: Questionnaire[] = [];

  get colWidth() {
    return ResponsibilityHelper.getColWidth(this);
  }

  async addNewQuestionnaire() {
    await this.questionnaireDatabaseService.addNewQuestionnaire(
      "New Questionnaire"
    );
  }

  get showLines() {
    return !ResponsibilityHelper.isMobile(this);
  }

  mounted() {
    this.$bind(
      "questionnaireCards",
      this.questionnaireDatabaseService.cardsBinding
    );
  }
}
