import Vue from "vue";
import { Component, Inject, Prop } from "vue-property-decorator";
import { QuestionnaireModel } from "@/model";
import { QuestionnaireDatabaseService } from "@/store/service/QuestionnaireDatabaseService";
import { TitleLine } from "@/components/lines";
import { SpeedDial } from "@/components/elements";
import { ShareLinkDialog } from "@/components/dialogs";
import { ResponsibilityHelper } from "@/store";
import { QuestionnaireLineContainer } from "@/components/containers";

@Component({
  components: {
    QuestionnaireLineContainer,
    TitleLine,
    SpeedDial,
    ShareLinkDialog
  }
})
export default class QuestionnaireCard extends Vue {
  @Prop({ required: true })
  questionnaire!: QuestionnaireModel;

  @Prop({ required: true })
  id!: string;

  @Prop({ default: false })
  showDetails!: boolean;

  @Inject(QuestionnaireDatabaseService.name)
  questionnaireDatabaseService!: QuestionnaireDatabaseService;

  shareLinkDialogOpen = false;
  shareLinkDialogText = "";

  get title() {
    return this.questionnaire?.title || "";
  }

  set title(newTitle: string) {
    this.questionnaireDatabaseService.updateQuestionnaireTitle({
      questionnaireID: this.id,
      questionnaireNewTitle: newTitle
    });
  }

  get isOwner() {
    return this.questionnaireDatabaseService.isUserOwner(this.questionnaire);
  }

  deleteQuestionnaire() {
    this.questionnaireDatabaseService.deleteQuestionnaire({
      questionnaireID: this.id
    });
  }

  shareQuestionnaire() {
    this.questionnaireDatabaseService
      .share({
        questionnaireID: this.id,
        sharedUntil: new Date(Date.now() + 3 * 24 * 60 * 60 * 1000)
      })
      .then(value => {
        this.shareLinkDialogText = location.host + "/#" + value;
        this.shareLinkDialogOpen = true;
      });
  }

  redirectToQuestionnairePage() {
    if (this.showDetails) {
      return;
    }
    this.$router.push({ name: "questionnaire", params: { id: this.id } });
  }

  get cardWidth() {
    return ResponsibilityHelper.cardWidth;
  }
}
