import Vue from "vue";
import { MembersDatabaseService } from "@/store/service/MembersDatabaseService";
import { CheckLineDatabaseService } from "@/store/service/CheckLineDatabaseService";
import { CheckListDatabaseService } from "@/store/service/CheckListDatabaseService";
import { QuestionnaireLineDatabaseService } from "@/store/service/QuestionnaireLineDatabaseService";
import { QuestionnaireDatabaseService } from "@/store/service/QuestionnaireDatabaseService";

function getNullableClassType<T>(): T | undefined {
  return undefined;
}

const services = Vue.observable({
  [MembersDatabaseService.name]: getNullableClassType<MembersDatabaseService>(),
  [CheckLineDatabaseService.name]: getNullableClassType<
    CheckLineDatabaseService
  >(),
  [CheckListDatabaseService.name]: getNullableClassType<
    CheckListDatabaseService
  >(),
  [QuestionnaireLineDatabaseService.name]: getNullableClassType<
    QuestionnaireLineDatabaseService
  >(),
  [QuestionnaireDatabaseService.name]: getNullableClassType<
    QuestionnaireDatabaseService
  >()
});

export default {
  init() {
    services[MembersDatabaseService.name] = new MembersDatabaseService();
    services[CheckLineDatabaseService.name] = new CheckLineDatabaseService();
    services[CheckListDatabaseService.name] = new CheckListDatabaseService();
    services[
      QuestionnaireLineDatabaseService.name
    ] = new QuestionnaireLineDatabaseService();
    services[
      QuestionnaireDatabaseService.name
    ] = new QuestionnaireDatabaseService();
  },
  provideAll() {
    return services;
  },

  provide(serviceName: string) {
    return services[serviceName];
  }
};
