import Vue from "vue";
import { Component, Emit, Prop } from "vue-property-decorator";

@Component
export default class EditableCardTitle extends Vue {
  @Prop({ default: "" })
  value?: string;

  isTextModified = false;

  get text() {
    return this.value;
  }

  set text(newText: string | undefined) {
    this.isTextModified = true;
    this.value = newText;
  }

  async onAccept() {
    if (this.isTextModified) {
      this.emitOnValueChanged();
      this.isTextModified = false;
    }
    (this.$refs.input as Vue & {
      focusout: () => void;
    }).focusout();
  }

  @Emit("input")
  private emitOnValueChanged() {
    return this.text;
  }
}
