import { CheckListModel } from "@/model";
import {
  MembersDatabaseService,
  ServiceFactory,
  AbstractDatabaseService
} from "@/store/service";
import { Timestamp } from "@/db";
import paths from "@/router/paths";

const CHECK_LIST_COLLECTION_NAME = "checkList";

export default class CheckListDatabaseService extends AbstractDatabaseService<
  CheckListModel
> {
  constructor() {
    super(CHECK_LIST_COLLECTION_NAME);
  }

  private get memberService() {
    return ServiceFactory.provide(
      MembersDatabaseService.name
    ) as MembersDatabaseService;
  }

  get cardsBinding() {
    return this.memberService
      .getCollectionReferenceWhereUserIsMember({
        collectionReference: this.getCollectionReference()
      })
      .where("deleted", "==", false);
  }

  async addUser(params: { checkListID: string }) {
    return await this.getCardReference(params)
      .get()
      .then(doc => {
        if (!doc.exists) {
          throw "Document not found";
        }
        if (this.memberService.isUserMemberOfDocument(doc.data() || {})) {
          return;
        }
        return this.updateDocument({
          documentId: params.checkListID,
          dataToUpdate: this.memberService.addMemberDocumentModel()
        });
      });
  }

  getCardReference(params: { checkListID: string }) {
    return this.getDocumentReference({
      documentID: params.checkListID
    });
  }

  isUserOwner(checkListData: CheckListModel) {
    return this.memberService.isUserOwnerOfDocument(checkListData);
  }

  async share(params: { checkListID: string; sharedUntil: Date }) {
    await this.updateDocument({
      documentId: params.checkListID,
      dataToUpdate: {
        publicUntil: Timestamp.fromDate(params.sharedUntil)
      }
    });
    return paths.checkListPage(params.checkListID);
  }

  async addNewCheckList(checkListTitle: string) {
    return await this.createDocument({
      documentData: this.memberService.addOwnerToDocumentData({
        title: checkListTitle,
        checkLines: [],
        deleted: false
      })
    });
  }

  async deleteCheckList(params: { checkListID: string }) {
    return await this.updateDocument({
      documentId: params.checkListID,
      dataToUpdate: {
        deleted: true
      }
    });
  }

  async updateCheckListTitle(params: {
    checkListID: string;
    checkListNewTitle: string;
  }) {
    return await this.updateDocument({
      documentId: params.checkListID,
      dataToUpdate: {
        title: params.checkListNewTitle
      }
    });
  }
}
