import Vue from "vue";
import { Component, Inject, Prop } from "vue-property-decorator";
import { CheckLineDatabaseService } from "@/store";
import { CheckLineModel } from "@/model";
import { AddCheckLine, CheckLine } from "@/components/lines";

type CheckLineBinder = {
  id: string;
  data: CheckLineModel;
};

@Component({
  components: {
    CheckLine,
    AddCheckLine
  }
})
export default class CheckLinesContainer extends Vue {
  checkLines: CheckLineBinder[] = [];

  @Prop({ required: true })
  cardId!: string;

  @Inject(CheckLineDatabaseService.name)
  checkLineDatabaseService!: CheckLineDatabaseService;

  addNewCheckLine(newCheckLineText: string) {
    this.checkLineDatabaseService.addNewCheckLine({
      checkListID: this.cardId,
      checkLineText: newCheckLineText,
      checkLineStatus: false
    });
  }

  onCheckLineStatusChange(params: { checkLineID: string; status: boolean }) {
    this.checkLineDatabaseService.updateCheckLineStatus({
      checkListId: this.cardId,
      checkLineID: params.checkLineID,
      checkLineStatus: params.status
    });
  }

  onCheckLineTextChange(params: { checkLineID: string; text: string }) {
    this.checkLineDatabaseService.updateCheckLineText({
      checkListID: this.cardId,
      checkLineID: params.checkLineID,
      checkLineText: params.text
    });
  }

  mounted() {
    this.$bind(
      "checkLines",
      this.checkLineDatabaseService.getCheckLinesBinding({
        checkListId: this.cardId
      })
    );
  }
}
