import { auth, firebaseUI, uiConfig } from "@/auth";

export default {
  buildAuthCard() {
    const authUI =
      firebaseUI.auth.AuthUI.getInstance() || new firebaseUI.auth.AuthUI(auth);
    authUI.start("#firebaseui-auth-container", uiConfig);
  },

  get divID() {
    return "firebaseui-auth-container";
  }
};
