import Vue from "vue";
import { Component, Emit, Prop } from "vue-property-decorator";
import { DeleteButton, EditableCardTitle } from "@/components/elements";

@Component({
  components: {
    EditableCardTitle,
    DeleteButton
  }
})
export default class TitleLine extends Vue {
  @Prop({ required: true })
  value!: string;

  @Prop({ default: true })
  editable!: boolean;

  editMode = false;

  modifiedText!: string;

  get text() {
    return this.value;
  }

  set text(newText: string) {
    this.value = newText;
  }

  async onAccept() {
    if (this.modifiedText !== this.text) {
      this.emitOnValueChanged();
    }
    this.editMode = false;
  }

  async onFocus() {
    if (this.editable) {
      this.editMode = true;
      this.modifiedText = this.text;
    }
  }

  @Emit("input")
  private emitOnValueChanged() {
    return this.modifiedText;
  }
}
