export default {
  homeDefault: "/",
  checkListPagePrefix: "/check-list/",
  checkListExplorer: "/check-list-explorer",
  rawCheckListPage: "/check-list/:id",
  questionnairePagePrefix: "/questionnaire/",
  questionnaireExplorer: "/questionnaire-explorer",
  rawQuestionnairePage: "/questionnaire/:id",

  checkListPage: function(checkListID: string) {
    return this.checkListPagePrefix + checkListID;
  },

  questionnairePage: function(questionnaireID: string) {
    return this.questionnairePagePrefix + questionnaireID;
  }
};
