import Vue from "vue";
import { Component, Inject, Prop } from "vue-property-decorator";
import { TitleLine } from "@/components/lines";
import { CheckListModel } from "@/model";
import { CheckListDatabaseService } from "@/store/service";
import { CheckLinesContainer } from "@/components/containers/CheckLinesContainer";
import { SpeedDial } from "@/components/elements";
import { ShareLinkDialog } from "@/components/dialogs";
import { ResponsibilityHelper } from "@/store";

@Component({
  components: {
    CheckLinesContainer,
    TitleLine,
    SpeedDial,
    ShareLinkDialog
  }
})
export default class CheckListCard extends Vue {
  @Prop({ required: true })
  checkList!: CheckListModel;

  @Prop({ required: true })
  id!: string;

  @Prop({ default: false })
  showDetails!: boolean;

  @Inject(CheckListDatabaseService.name)
  checkListDatabaseService!: CheckListDatabaseService;

  shareLinkDialogOpen = false;
  shareLinkDialogText = "";

  get title() {
    return this.checkList?.title || "";
  }

  set title(newTitle: string) {
    this.checkListDatabaseService.updateCheckListTitle({
      checkListID: this.id,
      checkListNewTitle: newTitle
    });
  }

  get isOwner() {
    return this.checkListDatabaseService.isUserOwner(this.checkList);
  }

  deleteCheckList() {
    this.checkListDatabaseService.deleteCheckList({
      checkListID: this.id
    });
  }

  shareCheckList() {
    this.checkListDatabaseService
      .share({
        checkListID: this.id,
        sharedUntil: new Date(Date.now() + 3 * 24 * 60 * 60 * 1000)
      })
      .then(value => {
        this.shareLinkDialogText = location.host + "/#" + value;
        this.shareLinkDialogOpen = true;
      });
  }

  redirectToCheckListPage() {
    if (this.showDetails) {
      return;
    }
    this.$router.push({ name: "checkList", params: { id: this.id } });
  }

  get cardWidth() {
    return ResponsibilityHelper.cardWidth;
  }
}
