import Vue from "vue";
import { Component, Emit, Inject, Prop } from "vue-property-decorator";
import { CheckLineModel } from "@/model";
import { CheckLineDatabaseService, ResponsibilityHelper } from "@/store";
import MarkedByModel from "@/model/MarkedByModel";

@Component
export default class CheckLine extends Vue {
  @Prop({ required: true })
  line!: CheckLineModel;

  @Prop({ required: true })
  id!: string;

  @Prop({ default: false })
  statusChangeDisabled_!: boolean;

  @Inject(CheckLineDatabaseService.name)
  checkLineDatabaseService!: CheckLineDatabaseService;

  editMode = false;

  editedText = "";

  isMarkedByShown = false;

  setMarkedByShown() {
    if (this.showMarkingOwner && !this.isMarkedByShown) {
      this.isMarkedByShown = true;
      setTimeout(() => {
        this.isMarkedByShown = false;
      }, 2000);
    }
  }

  get isMobile() {
    return ResponsibilityHelper.isMobile(this);
  }

  get markedBy() {
    return this.line.markedBy ? (this.line.markedBy as MarkedByModel).name : "";
  }

  get text() {
    return this.line.text;
  }

  get status() {
    return this.line.status || false;
  }

  set status(isChecked: boolean) {
    this.emitOnStatusChange(isChecked);
  }

  get statusChangeDisabled() {
    return !this.checkLineDatabaseService.canChangeStatus(this.line);
  }

  get showMarkingOwner() {
    return this.status && this.statusChangeDisabled;
  }

  async onFocus() {
    this.editMode = true;
    this.editedText = this.text || "";
  }

  async onAccept() {
    if (this.editedText !== this.text) {
      this.emitOnTextChange(this.editedText);
    }
    this.editMode = false;
  }

  @Emit("textChange")
  emitOnTextChange(newText: string) {
    return {
      checkLineID: this.id,
      text: newText
    };
  }

  @Emit("statusChange")
  emitOnStatusChange(newStatus: boolean) {
    return {
      checkLineID: this.id,
      status: newStatus
    };
  }
}
