import Vue from "vue";
import { Component, Emit } from "vue-property-decorator";

@Component
export default class AddQuestionnaireLine extends Vue {
  text = "";

  onEnter() {
    this.emitNewCheckLine();
    this.text = "";
  }

  @Emit("addQuestionnaireLine")
  private emitNewCheckLine() {
    return this.text;
  }
}
