import { MembersDatabaseService, ServiceFactory } from "@/store";
import { QuestionnaireLineModel } from "@/model";
import {
  QuestionnaireDatabaseService,
  AbstractDatabaseService
} from "@/store/service";
import { FieldValue } from "@/db";

const QUESTIONNAIRE_LINE_COLLECTION_NAME = "questionnaireLines";
const QUESTIONNAIRE_LINE_MARKED_BY_FIELD = "markedBy";

export default class QuestionnaireLineDatabaseService extends AbstractDatabaseService<
  QuestionnaireLineModel
> {
  constructor() {
    super(QUESTIONNAIRE_LINE_COLLECTION_NAME);
  }

  getQuestionnaireLinesBinding(params: { questionnaireId: string }) {
    return this.getCollectionReference({
      parent: this.getQuestionnaireReference(params.questionnaireId)
    });
  }

  async updateQuestionnaireLineText(params: {
    questionnaireLineID: string;
    questionnaireLineText: string;
    questionnaireID: string;
  }) {
    return await this.updateDocument({
      parent: this.getQuestionnaireReference(params.questionnaireID),
      documentId: params.questionnaireLineID,
      dataToUpdate: {
        text: params.questionnaireLineText
      }
    });
  }

  addNewQuestionnaireLine(params: {
    questionnaireID: string;
    questionnaireLineText: string;
  }) {
    this.createDocument({
      parent: this.getQuestionnaireReference(params.questionnaireID),
      documentData: {
        text: params.questionnaireLineText,
        [QUESTIONNAIRE_LINE_MARKED_BY_FIELD]: {}
      }
    });
  }

  isMarkedByUser(questionnaireLine: QuestionnaireLineModel) {
    if (!questionnaireLine.markedBy || !this.memberService.userID) {
      return false;
    }
    return this.memberService.userID in questionnaireLine.markedBy;
  }

  deleteQuestionnaireLine(params: {
    questionnaireID: string;
    questionnaireLineID: string;
  }) {
    this.deleteDocument({
      documentId: params.questionnaireLineID,
      parent: this.getQuestionnaireReference(params.questionnaireID)
    });
  }

  async markQuestionnaireLineByUser(params: {
    questionnaireLineID: string;
    questionnaireID: string;
    questionnaireLineMarkingStatus: boolean;
  }) {
    return await this.updateDocument({
      parent: this.getQuestionnaireReference(params.questionnaireID),
      documentId: params.questionnaireLineID,
      dataToUpdate: {
        [`${QUESTIONNAIRE_LINE_MARKED_BY_FIELD}.${this.memberService.userID}`]: params.questionnaireLineMarkingStatus
          ? this.memberService.userName
          : FieldValue.delete()
      }
    });
  }

  private get questionnaireService(): QuestionnaireDatabaseService {
    return ServiceFactory.provide(
      QuestionnaireDatabaseService.name
    ) as QuestionnaireDatabaseService;
  }

  private get memberService() {
    return ServiceFactory.provide(
      MembersDatabaseService.name
    ) as MembersDatabaseService;
  }

  private getQuestionnaireReference(questionnaireId: string) {
    return this.questionnaireService?.getCardReference({
      questionnaireID: questionnaireId
    });
  }
}
