import Vue from "vue";
import { Component, Inject } from "vue-property-decorator";
import { AuthService, UserRepository } from "@/store";

@Component
export default class AuthCard extends Vue {
  authService = AuthService;

  guestDialog = false;

  guestNick = "";

  @Inject(UserRepository.name)
  userRepository!: UserRepository;

  get isMainDialogVisible() {
    return !this.guestDialog;
  }

  get isGuestDialogVisible() {
    return this.guestDialog;
  }

  get divID() {
    return this.authService.divID;
  }

  openGuestDialog() {
    this.guestDialog = true;
  }

  closeGuestDialog() {
    this.guestDialog = false;
  }

  loginAsGuest() {
    this.userRepository.loginAsGuest(this.guestNick);
  }

  mounted() {
    AuthService.buildAuthCard();
  }
}
