import Vue from "vue";
import { Component, Inject } from "vue-property-decorator";
import { CheckListDatabaseService } from "@/store";
import { CheckListCard } from "@/components/cards";
import paths from "@/router/paths";
import { NotAllowedDialog } from "@/components/dialogs";

interface CheckList {
  id: string;
  data: {};
}

@Component({
  components: {
    CheckListCard,
    NotAllowedDialog
  }
})
export default class CheckListPage extends Vue {
  checkListCard: CheckList | null = null;

  id: string = this.$route.params.id;

  hasOpenPermission = false;

  @Inject(CheckListDatabaseService.name)
  checkListDatabaseService!: CheckListDatabaseService;

  get checklistData() {
    return this.checkListCard?.data || {};
  }

  mounted() {
    this.checkListDatabaseService
      .addUser({ checkListID: this.id })

      //TODO poprawić
      .catch(() => {
        this.$router
          .push({ path: paths.checkListExplorer })
          .then(() =>
            alert("You are not allowed to open checklist " + this.id)
          );
      })
      .then(() => {
        this.$bind(
          "checkListCard",
          this.checkListDatabaseService.getCardReference({
            checkListID: this.id
          })
        );
        this.hasOpenPermission = true;
      });
  }
}
