import { CheckLineModel } from "@/model";
import {
  CheckListDatabaseService,
  MembersDatabaseService,
  ServiceFactory
} from "@/store/service";
import { AbstractDatabaseService } from "@/store/service/AbstractDatabaseService";
import MarkedByModel from "@/model/MarkedByModel";

const CHECK_LINE_COLLECTION_NAME = "checkLines";
const CHECK_LINE_MARKED_BY_FIELD = "markedBy";

export default class CheckLineDatabaseService extends AbstractDatabaseService<
  CheckLineModel
> {
  constructor() {
    super(CHECK_LINE_COLLECTION_NAME);
  }

  getCheckLinesBinding(params: { checkListId: string }) {
    return this.getCollectionReference({
      parent: this.getChecklistReference(params.checkListId)
    });
  }

  async updateCheckLineStatus(params: {
    checkLineID: string;
    checkLineStatus: boolean;
    checkListId: string;
  }) {
    return await this.updateDocument({
      documentId: params.checkLineID,
      parent: this.getChecklistReference(params.checkListId),
      dataToUpdate: this.addMarkedByToCheckLineData({
        status: params.checkLineStatus
      })
    });
  }

  async updateCheckLineText(params: {
    checkLineID: string;
    checkLineText: string;
    checkListID: string;
  }) {
    return await this.updateDocument({
      parent: this.getChecklistReference(params.checkListID),
      documentId: params.checkLineID,
      dataToUpdate: {
        text: params.checkLineText
      }
    });
  }

  addNewCheckLine(params: {
    checkListID: string;
    checkLineText: string;
    checkLineStatus: boolean;
  }) {
    this.createDocument({
      parent: this.getChecklistReference(params.checkListID),
      documentData: {
        status: params.checkLineStatus,
        text: params.checkLineText
      }
    });
  }

  deleteCheckLine(params: { checkListID: string; checkLineID: string }) {
    this.deleteDocument({
      documentId: params.checkLineID,
      parent: this.getChecklistReference(params.checkListID)
    });
  }

  canChangeStatus(checkLine: CheckLineModel) {
    return (
      !checkLine.status ||
      (checkLine.markedBy as MarkedByModel).id === this.memberService.userID
    );
  }

  private get checkListService(): CheckListDatabaseService {
    return ServiceFactory.provide(
      CheckListDatabaseService.name
    ) as CheckListDatabaseService;
  }

  private get memberService() {
    return ServiceFactory.provide(
      MembersDatabaseService.name
    ) as MembersDatabaseService;
  }

  private getChecklistReference(checklistId: string) {
    return this.checkListService!.getCardReference({
      checkListID: checklistId
    });
  }

  private addMarkedByToCheckLineData(documentData: CheckLineModel) {
    if (!this.memberService.userID) {
      throw "Missing user ID";
    }

    if (!this.memberService.userName) {
      throw "Missing user name";
    }
    return {
      ...documentData,
      [CHECK_LINE_MARKED_BY_FIELD]: {
        id: this.memberService.userID,
        name: this.memberService.userName
      }
    };
  }
}
