import firebase from "firebase";
import { UserModel } from "@/model";
import Vue from "vue";

export default class UserRepository {
  constructor() {
    this.assignUserStateChange();
  }

  private store = Vue.observable({ user: this.getEmptyUser() });

  get user() {
    return this.store.user;
  }

  set user(user) {
    this.store.user = user;
  }

  get userUid() {
    return this.store.user?.uid;
  }

  async signOut() {
    await firebase.auth().signOut();
  }

  async loginAsGuest(guestNick: string) {
    await firebase
      .auth()
      .signInAnonymously()
      .then(() => {
        firebase
          .auth()
          .currentUser?.updateProfile({
            displayName: guestNick
          })
          .then(() => {
            this.updateUser(firebase.auth().currentUser);
          });
      });
  }

  private getEmptyUser(): UserModel | undefined {
    return undefined;
  }

  private assignUserStateChange() {
    firebase.auth().onAuthStateChanged(user => this.updateUser(user));
  }

  private updateUser(user: firebase.User | null) {
    this.user = user
      ? {
          name: user.displayName || "",
          avatar: user.photoURL || undefined,
          uid: user.uid
        }
      : undefined;
  }
}
