import Vue from "vue";
import { Component, Inject, Prop } from "vue-property-decorator";
import { QuestionnaireLineModel } from "@/model";
import { AddQuestionnaireLine, QuestionnaireLine } from "@/components/lines";
import { QuestionnaireLineDatabaseService } from "@/store/service/QuestionnaireLineDatabaseService";

type QuestionnaireLineBinder = {
  id: string;
  data: QuestionnaireLineModel;
};

@Component({
  components: {
    QuestionnaireLine,
    AddQuestionnaireLine
  }
})
export default class QuestionnaireLineContainer extends Vue {
  questionnaireLines: QuestionnaireLineBinder[] = [];

  @Prop({ required: true })
  cardId!: string;

  @Inject(QuestionnaireLineDatabaseService.name)
  questionnaireLineDatabaseService!: QuestionnaireLineDatabaseService;

  addNewQuestionnaireLine(newQuestionnaireLineText: string) {
    this.questionnaireLineDatabaseService.addNewQuestionnaireLine({
      questionnaireID: this.cardId,
      questionnaireLineText: newQuestionnaireLineText
    });
  }

  onQuestionnaireLineMarkingChange(params: {
    questionnaireLineID: string;
    status: boolean;
  }) {
    console.log(params);
    this.questionnaireLineDatabaseService.markQuestionnaireLineByUser({
      questionnaireID: this.cardId,
      questionnaireLineID: params.questionnaireLineID,
      questionnaireLineMarkingStatus: params.status
    });
  }

  onQuestionnaireLineTextChange(params: {
    questionnaireLineID: string;
    text: string;
  }) {
    this.questionnaireLineDatabaseService.updateQuestionnaireLineText({
      questionnaireID: this.cardId,
      questionnaireLineID: params.questionnaireLineID,
      questionnaireLineText: params.text
    });
  }

  mounted() {
    this.$bind(
      "questionnaireLines",
      this.questionnaireLineDatabaseService.getQuestionnaireLinesBinding({
        questionnaireId: this.cardId
      })
    );
  }
}
