import { UserRepository } from "@/store/repository/UserRepository";
import Vue from "vue";

function getNullableClassType<T>(): T | undefined {
  return undefined;
}

const repos = Vue.observable({
  [UserRepository.name]: getNullableClassType<UserRepository>()
});

export default {
  init() {
    repos[UserRepository.name] = new UserRepository();
  },
  provideAll() {
    return repos;
  },

  provide(repoName: string) {
    return repos[repoName];
  }
};
