import { RepositoryFactory, UserRepository } from "@/store";
import { CollectionReference } from "@/db";
import MemberRole from "@/enums/MemberRole";

const MEMBERS_FIELD = "members";
const MARKED_BY_FIELD = "markedBy";

const EDIT_ACCESS_ROLES = [MemberRole.Member, MemberRole.Owner];

export default class MembersDatabaseService {
  get userRepository() {
    return RepositoryFactory.provide(UserRepository.name);
  }

  getCollectionReferenceWhereUserIsMember(params: {
    collectionReference: CollectionReference;
  }) {
    if (!this.userID) {
      throw "Missing user ID";
    }
    return params.collectionReference.where(
      `${MEMBERS_FIELD}.${this.userID}.role`,
      "in",
      [MemberRole.Owner, MemberRole.Member]
    );
  }

  addOwnerToDocumentData<Model>(documentData: Model) {
    if (!this.userID) {
      throw "Missing user ID";
    }
    return {
      ...documentData,
      [MEMBERS_FIELD]: {
        [this.userID]: {
          role: MemberRole.Owner,
          name: this.userName
        }
      }
    };
  }

  addMarkedByToDocumentData<Model>(documentData: Model) {
    if (!this.userID) {
      throw "Missing user ID";
    }
    return {
      ...documentData,
      [MARKED_BY_FIELD]: {
        id: this.userID,
        name: this.userName
      }
    };
  }

  isUserMemberOfDocument(documentData: {}) {
    return (
      documentData[MEMBERS_FIELD] != undefined &&
      documentData[MEMBERS_FIELD][this.userID] != undefined &&
      EDIT_ACCESS_ROLES.includes(documentData[MEMBERS_FIELD][this.userID].role)
    );
  }

  isUserOwnerOfDocument(documentData: {}) {
    return (
      documentData[MEMBERS_FIELD] != undefined &&
      documentData[MEMBERS_FIELD][this.userID] != undefined &&
      documentData[MEMBERS_FIELD][this.userID].role === MemberRole.Owner
    );
  }

  addMemberDocumentModel() {
    return {
      [MEMBERS_FIELD + "." + this.userRepository?.userUid]: {
        role: MemberRole.Member,
        name: this.userName
      }
    };
  }

  get userName() {
    return this.userRepository?.user?.name;
  }

  get userID() {
    return this.userRepository?.userUid;
  }
}
