import Vue from "vue";
import { Component, Inject } from "vue-property-decorator";
import { CheckListCard } from "@/components/cards";
import { AddButton } from "@/components/elements";
import { CheckListDatabaseService } from "@/store/service";
import { ResponsibilityHelper } from "@/store";

interface CheckList {
  id: string;
  data: {};
}

@Component({
  components: {
    CheckListCard,
    AddButton
  }
})
export default class CheckListExplorer extends Vue {
  @Inject(CheckListDatabaseService.name)
  checkListDatabaseService!: CheckListDatabaseService;

  checkListCards: CheckList[] = [];

  get colWidth() {
    return ResponsibilityHelper.getColWidth(this);
  }

  async addNewCheckList() {
    await this.checkListDatabaseService.addNewCheckList("New Checklist");
  }

  get showCheckLines() {
    return !ResponsibilityHelper.isMobile(this);
  }

  mounted() {
    this.$bind("checkListCards", this.checkListDatabaseService.cardsBinding);
  }
}
