import { AbstractDatabaseService } from "@/store/service/AbstractDatabaseService";
import { MembersDatabaseService, ServiceFactory } from "@/store/service";
import { QuestionnaireModel } from "@/model";
import { Timestamp } from "@/db";
import paths from "@/router/paths";

const QUESTIONNAIRE_COLLECTION_NAME = "questionnaire";

export default class QuestionnaireDatabaseService extends AbstractDatabaseService<
  QuestionnaireModel
> {
  constructor() {
    super(QUESTIONNAIRE_COLLECTION_NAME);
  }

  private get memberService() {
    return ServiceFactory.provide(
      MembersDatabaseService.name
    ) as MembersDatabaseService;
  }

  get cardsBinding() {
    return this.memberService
      .getCollectionReferenceWhereUserIsMember({
        collectionReference: this.getCollectionReference()
      })
      .where("deleted", "==", false);
  }

  async addUser(params: { questionnaireID: string }) {
    return await this.getCardReference(params)
      .get()
      .then(doc => {
        if (!doc.exists) {
          throw "Document not found";
        }
        if (this.memberService.isUserMemberOfDocument(doc.data() || {})) {
          return;
        }
        return this.updateDocument({
          documentId: params.questionnaireID,
          dataToUpdate: this.memberService.addMemberDocumentModel()
        });
      });
  }

  getCardReference(params: { questionnaireID: string }) {
    return this.getDocumentReference({
      documentID: params.questionnaireID
    });
  }

  isUserOwner(questionnaireData: QuestionnaireModel) {
    return this.memberService.isUserOwnerOfDocument(questionnaireData);
  }

  async share(params: { questionnaireID: string; sharedUntil: Date }) {
    await this.updateDocument({
      documentId: params.questionnaireID,
      dataToUpdate: {
        publicUntil: Timestamp.fromDate(params.sharedUntil)
      }
    });
    return paths.questionnairePage(params.questionnaireID);
  }

  async addNewQuestionnaire(questionnaireTitle: string) {
    return await this.createDocument({
      documentData: this.memberService.addOwnerToDocumentData({
        title: questionnaireTitle,
        deleted: false
      })
    });
  }

  async deleteQuestionnaire(params: { questionnaireID: string }) {
    return await this.updateDocument({
      documentId: params.questionnaireID,
      dataToUpdate: {
        deleted: true
      }
    });
  }

  async updateQuestionnaireTitle(params: {
    questionnaireID: string;
    questionnaireNewTitle: string;
  }) {
    return await this.updateDocument({
      documentId: params.questionnaireID,
      dataToUpdate: {
        title: params.questionnaireNewTitle
      }
    });
  }
}
