import Vue from "vue";
import { Component, Emit, Prop } from "vue-property-decorator";

@Component
export default class ShareLinkDialog extends Vue {
  @Prop({ required: true })
  linkToCopy!: string;

  @Prop({ required: true })
  value!: boolean;

  get isDialogOpen() {
    return this.value;
  }

  set isDialogOpen(isOpen: boolean) {
    this.emitOnValueChanged(isOpen);
  }

  closeDialog() {
    this.isDialogOpen = false;
  }

  @Emit("input")
  private emitOnValueChanged(newValue: boolean) {
    return newValue;
  }
}
