import Vue from "vue";
import { Component, Inject, Watch } from "vue-property-decorator";
import { UserRepository } from "@/store";
import { UserModel } from "@/model";

@Component
export default class UserDetailsContainer extends Vue {
  @Inject(UserRepository.name)
  userRepository!: UserRepository;

  dialog = false;

  get userName() {
    return this.userRepository.user?.name;
  }

  get userAvatar() {
    return this.userRepository.user?.avatar;
  }

  get userAvatarExists() {
    return !!this.userAvatar;
  }

  get userInitials() {
    return this.getInitials(this.userName);
  }

  signOut() {
    this.userRepository
      .signOut()
      .then(_ => this.$router.push({ name: "home" }));
    this.dialog = false;
  }

  private getInitials(fullName?: string) {
    if (fullName) {
      const initials = fullName.split(" ").map(name => name.substr(0, 1));
      return initials.length > 1
        ? initials[0].concat(initials[initials.length - 1])
        : initials[0];
    }
  }
}
