import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";

@Component
export default class NotAllowedDialog extends Vue {
  @Prop({ default: "" })
  message!: string;

  @Prop({ required: true })
  redirectTo!: string;

  onAccept() {
    console.log(this.$route);
    this.$router.push({ path: this.redirectTo });
  }
}
