import Vue from "vue";
import { Component, Emit, Inject, Prop } from "vue-property-decorator";
import { QuestionnaireLineModel } from "@/model";
import { ResponsibilityHelper } from "@/store";
import { QuestionnaireLineDatabaseService } from "@/store/service/QuestionnaireLineDatabaseService";

@Component
export default class QuestionnaireLine extends Vue {
  @Prop({ required: true })
  line!: QuestionnaireLineModel;

  @Prop({ required: true })
  id!: string;

  @Prop({ default: false })
  statusChangeDisabled_!: boolean;

  @Inject(QuestionnaireLineDatabaseService.name)
  questionnaireLineDatabaseService!: QuestionnaireLineDatabaseService;

  editMode = false;

  editedText = "";

  get isMobile() {
    return ResponsibilityHelper.isMobile(this);
  }

  get markingCount() {
    const count = Object.keys(this.line.markedBy || {}).length;
    if (count > 9) {
      return "+9";
    }
    return count;
  }

  get text() {
    return this.line.text;
  }

  get status() {
    return this.questionnaireLineDatabaseService.isMarkedByUser(this.line);
  }

  set status(isChecked: boolean) {
    this.emitOnStatusChange(isChecked);
  }

  async onFocus() {
    this.editMode = true;
    this.editedText = this.text || "";
  }

  async onAccept() {
    if (this.editedText !== this.text) {
      this.emitOnTextChange(this.editedText);
    }
    this.editMode = false;
  }

  @Emit("textChange")
  emitOnTextChange(newText: string) {
    return {
      questionnaireLineID: this.id,
      text: newText
    };
  }

  @Emit("statusChange")
  emitOnStatusChange(newStatus: boolean) {
    console.log(Object.keys(this.line.markedBy || {}));
    return {
      questionnaireLineID: this.id,
      status: newStatus
    };
  }
}
