import { db, DocumentReference } from "@/db";

export default class AbstractDatabaseService<T> {
  protected collectionName;

  constructor(collectionName: string) {
    this.collectionName = collectionName;
  }

  protected async updateDocument(params: {
    documentId: string;
    dataToUpdate: T;
    parent?: DocumentReference;
  }) {
    if (params.parent) {
      return await params.parent
        .collection(this.collectionName)
        .doc(params.documentId)
        .update(params.dataToUpdate);
    }
    return await db
      .collection(this.collectionName)
      .doc(params.documentId)
      .update(params.dataToUpdate);
  }

  protected async createDocument(params: {
    documentData: T;
    parent?: DocumentReference;
  }) {
    if (params.parent) {
      return await params.parent
        .collection(this.collectionName)
        .add(params.documentData);
    }
    return await db.collection(this.collectionName).add(params.documentData);
  }

  protected async deleteDocument(params: {
    documentId: string;
    parent?: DocumentReference;
  }) {
    if (params.parent) {
      return await params.parent
        .collection(this.collectionName)
        .doc(params.documentId)
        .delete();
    }
    return await db
      .collection(this.collectionName)
      .doc(params.documentId)
      .delete();
  }

  protected getCollectionReference(params?: { parent?: DocumentReference }) {
    if (params && params.parent) {
      return params.parent.collection(this.collectionName);
    }
    return db.collection(this.collectionName);
  }

  protected getDocumentReference(params: {
    documentID: string;
    parent?: DocumentReference;
  }) {
    if (params.parent) {
      return params.parent
        .collection(this.collectionName)
        .doc(params.documentID);
    }
    return db.collection(this.collectionName).doc(params.documentID);
  }
}
