import Vue from "vue";
import { Component } from "vue-property-decorator";
import { UserDetailsContainer } from "@/components/containers";
import { NavLine } from "@/components/lines";

@Component({
  components: {
    UserDetailsContainer,
    NavLine
  }
})
export default class MyMenu extends Vue {
  drawer = false;

  openMenu() {
    this.drawer = true;
  }
}
