import Vue from "vue";
import { Component, Inject } from "vue-property-decorator";
import { QuestionnaireCard } from "@/components/cards";
import { NotAllowedDialog } from "@/components/dialogs";
import { QuestionnaireDatabaseService } from "@/store/service/QuestionnaireDatabaseService";
import paths from "@/router/paths";

interface Questionnaire {
  id: string;
  data: {};
}

@Component({
  components: {
    QuestionnaireCard,
    NotAllowedDialog
  }
})
export default class QuestionnairePage extends Vue {
  questionnaireCard: Questionnaire | null = null;

  id: string = this.$route.params.id;

  hasOpenPermission = false;

  @Inject(QuestionnaireDatabaseService.name)
  questionnaireDatabaseService!: QuestionnaireDatabaseService;

  get questionnaireData() {
    return this.questionnaireCard?.data || {};
  }

  mounted() {
    this.questionnaireDatabaseService
      .addUser({ questionnaireID: this.id })

      //TODO poprawić
      .catch(() => {
        this.$router
          .push({ path: paths.checkListExplorer })
          .then(() =>
            alert("You are not allowed to open questionnaire " + this.id)
          );
      })
      .then(() => {
        this.$bind(
          "questionnaireCard",
          this.questionnaireDatabaseService.getCardReference({
            questionnaireID: this.id
          })
        );
        this.hasOpenPermission = true;
      });
  }
}
