import Vue from "vue";

export default {
  getColWidth: function(component: Vue) {
    const maxWidth = 12;
    let columnCount: number;
    if (component.$vuetify.breakpoint.xl) {
      columnCount = 4;
    } else if (
      component.$vuetify.breakpoint.lg ||
      component.$vuetify.breakpoint.md
    ) {
      columnCount = 3;
    } else if (component.$vuetify.breakpoint.sm) {
      columnCount = 2;
    } else {
      columnCount = 1;
    }
    return maxWidth / columnCount;
  },

  isMobile: function(component: Vue) {
    return component.$vuetify.breakpoint.mobile;
  },

  cardWidth: 250
};
