









import Vue from "vue";
import { MyMenu } from "@/components/pages";
import { MainContainer } from "@/components/containers";

export default Vue.extend({
  name: "App",

  components: { MainContainer, MyMenu },

  methods: {
    async update() {
      this.prompt = false;
      await this.$workbox.messageSW({ type: "SKIP_WAITING" });
    }
  },
  data() {
    return {
      prompt: false
    };
  },
  created() {
    if (this.$workbox) {
      this.$workbox.addEventListener("waiting", () => {
        this.prompt = true;
      });
    }
  }
});
